import React from "react";
import MetsButton from "./UI/button/MetsButton";


const MetItem = ({ mets, met, metF }) => {
  function removeMets() {
    metF(met)
  }
  return (
    <div className="mets__item" >
      <h1 className="mets__title">{mets}</h1>
      <MetsButton style={ {backgroundColor: '#50C878'} }  onClick={removeMets}>remove</MetsButton>
    </div>
  )
}

export default MetItem