import React from 'react';
import '../styles/MetsList.scss';
import MetItem from '../components/MetItem';
import SubTitle from './UI/title/SubTitle'

const MetsList = ({ mets, remove }) => {

  const metF = (met) => {
    remove(mets.filter(ar => ar.id !== met.id))
  }

  return (
    <div className="mets">
      <SubTitle>Mets List</SubTitle>
      <div className="mets__inner">
        {
          mets.map((met) => {
            return (
              <MetItem mets={met.title} met={met} key={met.id} metF={metF}></MetItem>
            )
          })
        }
      </div>
    </div>
  )
}

export default MetsList