import React, { useState } from 'react'
import MetsInput from './UI/input/MetsInput'
import '../styles/MetsForm.scss'
import MetsButton from './UI/button/MetsButton'
import SubTitle from './UI/title/SubTitle'
const MetsForm = ({ create, mets },) => {
  const [newMets, setNewMets] = useState('')

  const createNewMets = (e,) => {
    e.preventDefault()
    const newMetss = {
      id: Math.floor(Math.random() * 10000), title: newMets,
    }
    create(newMetss)
    setNewMets('')
  }

  return (
    <div className="form">
      <SubTitle>Creat Mets</SubTitle>
      <form className="form__inner">
        <MetsInput
          value={newMets}
          onChange={e => setNewMets(e.target.value)}
          placeholder="Заметка"
        />
        <MetsButton type="submit" style={{ color: "#fff" }} onClick={createNewMets}>Create</MetsButton>
      </form>
    </div>
  )
}

export default MetsForm