import React, { useState } from 'react';
import './styles/App.scss'
import MainTitle from './components/MainTitle';
import MetsList from './components/MetsList';
import MetsForm from './components/MetsForm';


function App() {
  const [mets, setMets] = useState([
    { id: Math.floor(Math.random() * 10000), title: 'mets1' },
    { id: Math.floor(Math.random() * 10000), title: 'mets2' },
    { id: Math.floor(Math.random() * 10000), title: 'mets3' },
    { id: Math.floor(Math.random() * 10000), title: 'mets4' },
    { id: Math.floor(Math.random() * 10000), title: 'mets5' },
    { id: Math.floor(Math.random() * 10000), title: 'mets6' },
  ])
  const removeMets = (met) => {
    setMets(met)
  }
  const createMets = (newMets) => {
    setMets([newMets, ...mets,])
  }

  return (
    <div className="App">
      <div className="container">
        <MainTitle mets={mets}></MainTitle>
        <MetsForm create={createMets} mets={mets} />
        <MetsList mets={mets} remove={removeMets} />
      </div>
    </div>
  );
}

export default App;
